<template>
  <div>
    <h1>this is 电影</h1>
    <v-back></v-back><v-search></v-search>
    <p>动态路由跳转</p>
    <ul>
      <li v-for="item in moves" :key="item.id" @click="goMove(item.name)">
        <img :src="item.imgs" alt="" />
        <div class="name">
          <p>电影名字:{{ item.name }}</p>
          <p>价格:<span>$</span>{{ item.price | filterFixed(2) }}</p>
          <p class="p2">上映时间:{{ item.time | filterTime }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      moves: [
        {
          id: 1,
          name: "Call Me by Your Name",
          imgs:
            "https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3070263195,2736540422&fm=26&gp=0.jpg",
          price: 39.0,
          time: 1603532263457,
        },
        {
          id: 2,
          name: "Joker",
          imgs:
            "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1494432210,3389040870&fm=26&gp=0.jpg",
          price: 29.99,
          time: 1605532233457,
        },
        {
          id: 3,
          name: "La La Land",
          imgs:
            "https://dss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=1882786399,2043639870&fm=58&s=1841A94CCF309A5D187EA09E0300408B",
          price: 39.9,
          time: 1605522263457,
        },
        {
          id: 4,
          name: "Le bleu est une couleur chaude",
          imgs:
            "https://dss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=1702528401,2000929969&fm=58&s=541059974D33459A487855D20300D0B1",
          price: 28.99,
          time: 1602532263457,
        },
      ],
    };
  },
  methods: {
    goMove(id, name) {
      this.$router.push("/moveDetail/" + id);
    },
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
  padding: 10px 0 0 0;
}
ul li {
  width: 100vw;
  height: 2.5rem;

  margin-top: 0.2rem;
  display: flex;
  background: #f6f6f6;
}
ul li img {
  width: 2rem;
  height: 2.5rem;
}
ul li .name {
  flex: 1;
}
ul li .name p {
  width: 1.5rem;
  line-height: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 0.5rem;
  padding: 0 0.2rem;
}
ul li .name p:nth-child(1) {
  color: skyblue;
}
ul li .name p span {
  color: red;
}
.p2 {
  font-size: 0.14rem;
  color: rgb(0, 255, 13);
}
</style>


